<template>
  <div class="settle">
      <div class="detail">
        <p class="total">
          <label>数量：</label>
          <span>{{total}}</span>
        </p>
        <p class="price">
          <label>价格：</label>
          <span>￥{{price | formatMoney}}</span>
        </p>
      </div>
      <div class="btn" @click="submit">
        {{confirmText}}
      </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      return {

      }
    },
    filters: {
      formatMoney(value){
        return value.toFixed(2)
      }
    },
    props: {
      confirmText: {
        type: String,
        default: '购物车'
      },
      total: {
        type: Number,
        default: 0
      },
      price: {
        type: Number,
        default: 0.00
      }
    },
    methods: {
      submit() {
        this.$emit('submit', {
          total: this.total,
          price: this.price
        })
      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"

    .settle
      display flex
      width 100%
      height 100%
      .detail
        display flex
        flex 1
        height 100%
        align-items center
        background #F0F0F0
        .total,.price
          margin-left 30px
          label
            color #4D4D4D
            font-size $font-size-small
          span
            color #EB4747
            font-size $font-size-medium-m
            font-weight bold
      .btn
        display flex
        width 110px
        height 100%
        align-items center
        justify-content center
        color #fff
        background #EB4747
        font-size $font-size-medium-m


</style>



