<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">我的购物车</span>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <div class="list">
            <!-- 在组件上面遍历的时候，需要加上:key，否则事件中的item取不到 https://vuejs.org/v2/guide/list.html#key -->
            <left-swipe v-for="(item,index) in shoppingCartItems" @delete="deleteItem(item.id)" :key="index">
              <good-item
              class="item"
              :item="item"
              @count="calc"
              >
              </good-item>
            </left-swipe>
          </div>
        </scroll>
        <div class="settle-wrapper" v-show="total">
          <settle @submit="submit" :price="price" :total="total"></settle>
        </div>
      </div>
    </div>
  </transition>
</template>


<script type="text/ecmascript-6">
  import LeftSwipe from 'base/left-swipe/left-swipe'
  import GoodItem from 'base/good-item/good-item'
  import Scroll from 'base/scroll/scroll'
  import Settle from 'base/settle/settle'
  import Search from 'components/purchase/search'
  import {mapActions,mapGetters} from 'vuex'

  export default {
    data () {
      return {

      }
    },
    computed: {
      ...mapGetters([
        'shoppingCartItems'
      ]),
      total() {
        let tempTotal = 0
        for(let key in this.shoppingCartItems){
          tempTotal += this.shoppingCartItems[key].count
        }
        console.log('12')
        return tempTotal
      },
      price() {
        let tempPrice = 0
        for(let key in this.shoppingCartItems){
          let value = this.shoppingCartItems[key]
          tempPrice += value.price * value.count
        }
        console.log('11')
        return tempPrice
      }
    },
    created () {
      console.log(this.shoppingCartItems)
    },
    methods: {
      back() {
        this.$router.back()
      },
      deleteItem(id){
        let shoppingCartItems = {}
        for(let key in this.shoppingCartItems){
          if(id !== this.shoppingCartItems[key].id){
            shoppingCartItems[key] = this.shoppingCartItems[key]
          }
        }
        this.setShoppingCart(shoppingCartItems)
      },
      // 计算数量和价格
      calc(item){
        this.selectedGoods[item.id] = item
        let tempTotal = 0
        let tempPrice = 0
        for(let key in this.selectedGoods){
          let value = this.selectedGoods[key]
          tempTotal += value.count
          tempPrice += value.price * value.count
        }
        this.total = tempTotal
        this.price = tempPrice
        this.item = item
      },
      // 跳转到提交订单模块
      submit(){
      },
      ...mapActions([
        'setShoppingCart'
      ])
    },
    components: {
      LeftSwipe,
      Search,
      Settle,
      GoodItem,
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    z-index 1
    background $color-background
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      display flex
      justify-content center
      .content
        width 100%
        overflow hidden
        margin-top 10px
        .list
          .item
            height 110px
      .settle-wrapper
        position fixed
        left 0
        right 0
        bottom 0
        height 45px

</style>




